import React, { useState, useRef, useEffect, useCallback } from 'react';
import axios from 'axios';
import {
  Box,
  Container,
  TextField,
  IconButton,
  Typography,
  Paper,
  Avatar,
  CircularProgress,
  Stack,
  useTheme,
  AppBar,
  Toolbar,
  Tooltip,
  useMediaQuery
} from '@mui/material';
import {
  Send as SendIcon,
  SmartToy as BotIcon,
  Person as PersonIcon,
  MoreVert as MoreIcon
} from '@mui/icons-material';

const API_BASE_URL = 'https://phosai-dbec65d5-46be-45ae-9062.cranecloud.io';

const MessageBubble = ({ message, theme }) => {
  const isUser = message.role === 'user';

  const formatTimestamp = (timestamp) => {
    return new Intl.DateTimeFormat('en-US', {
      hour: 'numeric',
      minute: 'numeric',
      hour12: true,
    }).format(new Date(timestamp));
  };

  return (
    <Box sx={{ display: 'flex', justifyContent: isUser ? 'flex-end' : 'flex-start', mb: 2 }}>
      {!isUser && (
        <Avatar sx={{ bgcolor: theme.palette.primary.main, mr: 1, width: 32, height: 32 }}>
          <BotIcon sx={{ fontSize: 20 }} />
        </Avatar>
      )}
      <Paper
        elevation={3}
        sx={{
          p: 2,
          maxWidth: '75%',
          borderRadius: 3,
          bgcolor: isUser ? 'primary.main' : 'background.paper',
          color: isUser ? 'common.white' : 'text.primary',
          wordBreak: 'break-word',
        }}
      >
        <Typography variant="body1">{message.content}</Typography>
        <Typography variant="caption" sx={{ display: 'block', mt: 0.5, color: isUser ? 'rgba(255,255,255,0.7)' : 'text.secondary' }}>
          {formatTimestamp(message.timestamp)}
        </Typography>
      </Paper>
      {isUser && (
        <Avatar sx={{ bgcolor: 'grey.400', ml: 1, width: 32, height: 32 }}>
          <PersonIcon sx={{ fontSize: 20 }} />
        </Avatar>
      )}
    </Box>
  );
};

const Chats = ({ agentId, targetLang, botName }) => {
  const theme = useTheme();
  const messagesEndRef = useRef(null);
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  const scrollToBottom = useCallback(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, []);

  useEffect(() => {
    scrollToBottom();
  }, [messages, scrollToBottom]);

  const handleSendMessage = async (e) => {
    e.preventDefault();
    if (!input.trim() || isLoading) return;

    const userMessage = { role: 'user', content: input.trim(), timestamp: new Date().toISOString() };
    setMessages((prev) => [...prev, userMessage]);
    setInput('');
    setIsLoading(true);
    setError(null);

    try {
      const formData = new FormData();
      formData.append('agent_id', agentId);
      formData.append('query', userMessage.content);
      formData.append('target_lang', targetLang);
      console.log(formData)
      const response = await axios.post(`${API_BASE_URL}/agents/conversations`, formData);

      if (response.data?.answer) {
        const botMessage = { role: 'assistant', content: response.data.answer, timestamp: new Date().toISOString() };
        setMessages((prev) => [...prev, botMessage]);
      } else {
        throw new Error('Invalid response from server');
      }
    } catch (error) {
      console.error('API Error:', error);
      setError(error.message);
      setMessages((prev) => [...prev, { role: 'assistant', content: 'Oops! Something went wrong.', timestamp: new Date().toISOString() }]);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', height: '100vh', maxWidth: '100%', bgcolor: 'grey.50' }}>
      
      {/* HEADER - Always Visible */}
      <AppBar position="sticky">
        <Toolbar>
          <Avatar sx={{ bgcolor: 'primary.dark', mr: 2, width: 40, height: 40 }}>
            <BotIcon sx={{ fontSize: 24 }} />
          </Avatar>
          <Typography variant="h6">{botName}</Typography>
          <Box sx={{ flexGrow: 1 }} />
          <Tooltip title="Options">
            <IconButton color="inherit">
              <MoreIcon />
            </IconButton>
          </Tooltip>
        </Toolbar>
      </AppBar>

      {/* CHAT MESSAGES - Scrollable */}
      <Box sx={{ flex: 1, overflowY: 'auto', p: 2 }}>
        <Container maxWidth="sm">
          <Stack spacing={2}>
            {messages.map((message, index) => (
              <MessageBubble key={index} message={message} theme={theme} />
            ))}
            <div ref={messagesEndRef} />
          </Stack>
        </Container>
      </Box>

      {/* INPUT FIELD - Always Visible */}
      <Paper
        component="form"
        onSubmit={handleSendMessage}
        sx={{
          p: 2,
          bgcolor: 'background.paper',
          borderTop: '1px solid',
          borderColor: 'divider',
          position: 'sticky',
          bottom: 0,
          width: '100%',
        }}
      >
        <Container maxWidth="sm">
          <Box sx={{ display: 'flex', gap: 1 }}>
            <TextField
              fullWidth
              variant="outlined"
              placeholder="Type your message..."
              value={input}
              onChange={(e) => setInput(e.target.value)}
              disabled={isLoading}
              sx={{
                '& .MuiOutlinedInput-root': {
                  borderRadius: 3,
                },
                bgcolor: 'white',
              }}
            />
            <IconButton type="submit" disabled={!input.trim() || isLoading} sx={{ bgcolor: input.trim() ? 'primary.main' : 'grey.300', color: 'white' }}>
              {isLoading ? <CircularProgress size={24} color="inherit" /> : <SendIcon />}
            </IconButton>
          </Box>
        </Container>
      </Paper>
    </Box>
  );
};

export default Chats;
